.App {
  width: 100vw;
  height: 100vh;
}

body{
  margin: 0;
  padding: 0;
}

.NavBar{
  background-color: #080f44;
  width: 100%;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}
.NavBar .leftSide{
  flex: 50;
  display: flex;
  justify-content: center;
  align-items: center;
}
.NavBar .leftSide .links{
    max-height: 80px;
}
.NavBar .leftSide .links  a {
  text-decoration: none;
  color: white;
  font-size: 25px;
  margin-left: 15px;
}
.NavBar .rightSide{
  flex: 50;
}

@media only screen and (max-width:850px) {
  .NavBar .leftSide .links a{
    font-size: 20px;
  }
}

@media only screen and (max-width:750px) {
  .NavBar .leftSide .links a{
    font-size: 15px;
  }
}





